import { PDFDocument, StandardFonts, rgb, degrees } from 'pdf-lib'
import { getStore, updStore, ObjectKeys, parseDateMMDDYY } from '../../../stores/Utils';


function blobfromArray(pdfBytes) {
  let blob = new Blob([pdfBytes], { type: "application/pdf" });
  let urlCreator = window.URL || window.webkitURL;
  return urlCreator.createObjectURL(blob);
}


let heightPg = 0;

let heiItm = 13;



let timesRomanFont = null;
let TimesRomanBold = null;
  




export const Comprobante2Pdf = async (data) => {
    


    const pdfDoc = await PDFDocument.create()
    timesRomanFont = await pdfDoc.embedFont(StandardFonts.TimesRoman);
    TimesRomanBold = await pdfDoc.embedFont(StandardFonts.TimesRomanBold);
   
  
  
  
    let page = pdfDoc.addPage()
    const { width, height } = page.getSize()
    const fontSize = 10
  
    heightPg =  height - 40;
  
    page.drawText( data.entryName || "Comprobante Manual", {
      x: 50,
      y:  heightPg,
      size: 14,
      font: TimesRomanBold,
      color: rgb(0, 0, 0),
    })
    const comprobanteWidth = TimesRomanBold.widthOfTextAtSize(data.comprobanteId , 14);

    const comprobanteLblWidth = timesRomanFont.widthOfTextAtSize("Comprobante:  " , 13);

    page.drawText("Comprobante:  "  , {
        x: 580 - comprobanteLblWidth - comprobanteWidth,
        y:  heightPg,
        size: 13,
        font: timesRomanFont,
        color: rgb(0, 0, 0),
    })
  
    page.drawText(data.comprobanteId + "" , {
        x: 580 - comprobanteWidth,
        y:  heightPg,
        size: 14,
        font: TimesRomanBold,
        color: rgb(0, 0, 0),
    })

    heightPg = heightPg -25;

    
    const docWidth = timesRomanFont.widthOfTextAtSize("Fecha:  " , 13);
    const docWoWidth = TimesRomanBold.widthOfTextAtSize(parseDateMMDDYY(data.date) +"", 14);

    page.drawText("Fecha:  " , {
        x: 580 - docWidth - docWoWidth,
        y:  heightPg,
        size: 13,
        font: timesRomanFont,
        color: rgb(0, 0, 0),
    })
  
    page.drawText(parseDateMMDDYY(data.date) + ""  , {
        x: 580 - docWoWidth,
        y:  heightPg,
        size: 14,
        font: TimesRomanBold,
        color: rgb(0, 0, 0),
    })

    heightPg = heightPg -25;
  
   
    let arr = data?.operations || [];
     
      heightPg = heightPg -18;

        page.drawText("Cuenta", {
          x: 20,
          y:  heightPg,
          size: 13,
          font: TimesRomanBold,
          color: rgb(0, 0, 0),
        })

        page.drawText("Sub Cuenta", {
            x: 170,
            y:  heightPg,
            size: 13,
            font: TimesRomanBold,
            color: rgb(0, 0, 0),
        })

        page.drawText("Documento", {
          x: 320,
          y:  heightPg,
          size: 13,
          font: TimesRomanBold,
          color: rgb(0, 0, 0),
      })




        const debTWidth = TimesRomanBold.widthOfTextAtSize( "Debito", 13);

        page.drawText("Debito", {
            x: 450 - debTWidth ,
            y:  heightPg,
            size: 13,
            font: TimesRomanBold,
            color: rgb(0, 0, 0),
        })  
        const creditTWidth = TimesRomanBold.widthOfTextAtSize("Credito", 13);

        page.drawText("Credito", {
            x: 530 - creditTWidth,
            y:  heightPg,
            size: 13,
            font: TimesRomanBold,
            color: rgb(0, 0, 0),
        }) 
   
        heightPg = heightPg -8;
        page.drawRectangle({
            x: 15,
            y: heightPg + 1,
            width: 500,
            height: 0.95,
            color: rgb(0,0,0),
            borderColor: rgb(0, 0, 0),
        });
      Array.isArray(arr) && arr.map((item, Ind)=>{
  
        heightPg = heightPg -16;
  
        if(item?.account?.name){
            page.drawText(item?.account?.name + "", {
              x: 20,
              y:  heightPg,
              size: fontSize,
              font: timesRomanFont,
              color: rgb(0, 0, 0),
            })
      
        }
        if(item?.sub_account?.subAccountName){
          page.drawText(item?.sub_account?.subAccountName + "", {
            x: 170,
            y:  heightPg,
            size: fontSize,
            font: timesRomanFont,
            color: rgb(0, 0, 0),
          })
    
        }
        page.drawText(item?.document + "", {
          x: 340,
          y:  heightPg,
          size: fontSize,
          font: timesRomanFont,
          color: rgb(0, 0, 0),
        })
        
        let debit = item?.debit?(item?.debit*1)?.toFixed(2):"0.00";
        const debitWidth = timesRomanFont.widthOfTextAtSize(debit + "", fontSize);

        page.drawText(debit + "", {
            x: 450 - debitWidth,
            y:  heightPg,
            size: fontSize,
            font: timesRomanFont,
            color: rgb(0, 0, 0),
        })

        let credit = item?.credit?(item?.credit*1)?.toFixed(2):"0.00";
        const creditWidth = timesRomanFont.widthOfTextAtSize(credit + "", fontSize);

        page.drawText(credit + "", {
            x: 530 - creditWidth,
            y:  heightPg,
            size: fontSize,
            font: timesRomanFont,
            color: rgb(0, 0, 0),
        })
    
        
      })
      heightPg = heightPg - 28;
      page.drawRectangle({
        x: 15,
        y: heightPg + 1,
        width: 500,
        height: 0.85,
        color: rgb(0,0,0),
        borderColor: rgb(0, 0, 0),
    });
  
    heightPg = heightPg - 18;
    page.drawText( "Detalles: ", {
        x: 50,
        y:  heightPg,
        size: 12,
        font: timesRomanFont,
        color: rgb(0, 0, 0),
      })

    heightPg = heightPg - 18;
    page.drawText(data.description?data.description:"", {
        x: 70,
        y:  heightPg,
        size: 11,
        font: timesRomanFont,
        color: rgb(0, 0, 0),
    })
    
    const pdfBytes = await pdfDoc.save();
    let blobUrl = blobfromArray(pdfBytes)
   
  
    window.open(blobUrl)
  }
  