export const getall_operations = async (cb) => {
    let bdyq2 = {...getStore(qryStore())?.all_opera};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId
    };

    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td && !td.error) {

        let allMnt = Array.from(Array(12).keys());
        let obj = {}
        td.map(cmpId=>{
            let keyId = cmpId.account + "_" + (cmpId.subAccount?cmpId.subAccount:"");
            let month = (new Date(cmpId.date)).getMonth();

            if(!obj[keyId]){
                obj[keyId] = {}
                allMnt.map(mnt=>{
                    obj[keyId][mnt] = 0;
                })
            }
            
            obj[keyId][month] =  parseFloatToDecimal(obj[keyId][month],2)*1 +( cmpId["type"] === "credit" ? parseFloatToDecimal(cmpId["amount"],2)*-1: parseFloatToDecimal(cmpId["amount"],2)*1);
            
          })
        updStore("comprobante_list", sortArrByK(td,"date",1));

        updStore("comprobante_by_doc", GroupbyDoc(td));
        cb && cb();
    }
};





export const search_operations = async (cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.filter_search_opera};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        //":account": frm?.account,
        //":subAccount": frm?.subAccount,
        ":date1": getStore("date_range_comps")?.initDate,
        ":date2": getStore("date_range_comps")?.lastDate
    }
    
    getStore("search_acc_record").split(" ").map((qry,inDq)=>{
        if(qry){
            bdyq2.params[":search"+inDq] = qry.trim();
        }
    })

    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td && !td.error) {
       

        updStore("comprobante_list", td);
        updStore("comprobante_by_doc", GroupbyDoc(convertObj2Array(td)));
        cb && cb();
    }
};


export const filter_operations = async (cb) => {
    let bdyq2 = {...getStore(qryStore())?.filter_search_opera};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        ":date1": getStore("date_range_comps")?.initDate - 10,
        ":date2": getStore("date_range_comps")?.lastDate
    }
    getStore("search_acc_record").split(" ").map((qry,inDq)=>{
        if(qry){
            bdyq2.params[":search"+(inDq+1)] = qry.trim();
        }
    })

    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td && !td.error) {
        updStore("comprobante_list", td);
        var arr = [];
        td &&
          ObjectKeys(td).map((o) => {

            
            if(
                td[o].date > getStore("date_range_comps")?.initDate - 10 && 
                td[o].date <= getStore("date_range_comps")?.lastDate
            ){
                arr.push(td[o]);
            }
          });
        updStore("comprobante_by_doc", GroupbyDoc(arr));
        cb && cb();
    }
};


