export function GroupbyDoc(arr1) {
    var obj = {};
    if (arr1.length > 0) {
      arr1.map((s) => {
        let ddt =  s["date"];
        let keyId = ddt + "_" +  s["comprobanteId"];
        // if(s.account==="Rs1CzHXPTG5TqbmH" && !s.subAccount){}
        if (!obj[keyId]) {
            obj[keyId] = {};
            let ggg = [
                "date",
                "description",
                "entryName",
                "comprobanteId",
                "employeeId",
                
            ]
            ggg.forEach(fld => {
                obj[keyId][fld] = s[fld];
            });
            obj[keyId]["amount"] = 0;
            obj[keyId]["balance"] = 0;
            obj[keyId]["operations"] = [];
        }
        
        if(s["type"]){
            //obj[keyId]["balance"] =  parseFloatToDecimal(obj[keyId]["balance"],2)*1 +( s["type"] === "credit" ? parseFloatToDecimal(s["amount"],2)*-1: parseFloatToDecimal(s["amount"],2)*1);
            obj[keyId]["operations"].push(convert4acc(s));
        }
    
      });
    }
    else {
      obj = null;
    }

    validateComprobantes(obj);
    return obj;
  }


  export function validateComprobantes(obj) {
    updStore("has_filter_review",null);
    updStore("has_filter_comp_error",null);

    //Log(getStore("comp_under_review"));
    ObjectKeys(obj).map(cmpId=>{
       
        if(getStore("comp_under_review")?.inReview?.[obj[cmpId].comprobanteId]){
            //Log("has_filter_review",obj[cmpId].comprobanteId);
            updStore("has_filter_review",true)
        }
        if(getStore("comp_under_review")?.hasError?.[obj[cmpId].comprobanteId]){
            //console.log("err",obj[cmpId].comprobanteId);
            updStore("has_filter_comp_error",true)
        }
    })
  }


  export function convert4acc(s) {
    let nItm = {};
    nItm["account"] =  getStore("account_ids")?.[s["account"]];
    nItm["sub_account"] = getStore("sub_account_ids")?.[s["subAccount"]];
    nItm["comprobanteId"] = s["comprobanteId"];
    nItm["operationId"] = s["operationId"];
    nItm["document"] = s["document"];
    nItm["type"] = s["type"];
    let amm = parseFloatToDecimal(s["amount"],2);
    if(s["type"]==="credit"){
        nItm["debit"] = 0;
        nItm["credit"] = amm;
    }
    else if(s["type"]==="debit"){
        nItm["credit"] = 0;
        nItm["debit"] = amm;
    }
    return nItm;
  }


  export const sortComprobantesByType = (td) =>
    callbackOnMemory(
      "sortComprobantesByType",
      [td],
      () => {
            let key = "type";
            let sortedA = td && td.sort((a, b) => {
                let A =a[key];
                let B = b[key];
                if (A === B) {
                    return b[B] - a[A];
                 }
                 return B > A ? 1 : -1;
            });
            return sortedA;
        }
    );






    const sortArrByK = (td,key, order) =>{
        return td && td.sort((a, b) => {
            let A =a[key]
            let B = b[key]
            if (A < B) {
                return order ? -1 : 1;
            }
            if (A > B) {
                return order ? 1 : -1;
            }
            return 0;
        });
         
    }



export const rangeArr = (obj, range) =>
callbackOnMemory(
	"rangeArr",
	[obj,range],
	() => {
        return obj ? ObjectKeys(obj).slice(0,range) : [];
    }
);



export function prepareCompDatatoCvs() {
    const reduc = (itm)=>{
        let acc = getStore("account_ids")?.[itm.account];
        let subacc = getStore("sub_account_ids")?.[itm.subAccount];
        let newObj = {
            fecha: parseDateMMDDYY(itm.date), 
            documento:   itm.document,
            comprobante:   itm.comprobanteId,
            tipo_cuenta: itm.type, 
            importe: itm.amount,
            cuenta: acc.code+"", nombre: acc.name
        };
        if(subacc && subacc.subAccount){
            newObj.sub_cuenta =  subacc.subAccount;
            newObj.nombre_sub_cuenta = subacc.subAccountName;
        }
        return newObj;
    }
    return  getStore("comprobante_list").map(reduc);
}






