export const getall_employee = async (cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.all_employee};
    bdyq2.params =  {
        businessId:  getStore("profileUser")?.businessId
    }

    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("employees_list", sortEmployees(convertObj2Array(td),"code"));
        updStore("employees_ids", convertArray2Obj(convertObj2Array(td),"employeeId"));
        cb && cb();
    }
};




