export function GroupbyDoc(arr1) {
    var obj = {};
    if (arr1.length > 0) {
      arr1.map((s) => {
        let ddt =  s["date"];
        let keyId = ddt + "_" +  s["comprobanteId"];
        if (!obj[keyId]) {
            obj[keyId] = {};
            let ggg = [
                "date",
                "description",
                "entryName",
                "comprobanteId",
                "employeeId",
                "document"
                
            ]
            ggg.forEach(fld => {
                obj[keyId][fld] = s[fld];
            });
            obj[keyId]["amount"] = 0;
            obj[keyId]["balance"] = 0;
            obj[keyId]["operations"] = [];
        }
        
        if(s["type"]){
            obj[keyId]["balance"] =  parseFloatToDecimal(obj[keyId]["balance"],2)*1 +( s["type"] === "debit" ? parseFloatToDecimal(s["amount"],2)*1: 0);
            obj[keyId]["operations"].push(convert4acc(s));
        }
      });
    }
    else {
      obj = null;
    }

    return obj;
  }



  export function convert4acc(s) {
    let nItm = {};
    nItm["employeeId"] =  getStore("employees_ids")?.[s["employeeId"]];
    nItm["comprobanteId"] = s["comprobanteId"];
    nItm["document"] = s["document"];
    nItm["date"] = s["date"];
    if(s["type"]==="credit"){
        nItm["debit"] = 0;
        nItm["credit"] = s["amount"];
        
    }
    else if(s["type"]==="debit"){
        nItm["credit"] = 0;
        nItm["debit"] = s["amount"];
    }
    return nItm;
  }



  
//  sorting episodes comparing inputs to save in cache
export const sortEmployees = (td, key) =>
callbackOnMemory(
	"sortEmployees",
	[td],
	() => {
        let order = true;
		let sortedA = td && td.sort((a, b) => {
			let A = a[key] * 1;
			let B = b[key] *1;
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);


