export const sortComprobantesByDate = (td) =>
callbackOnMemory(
	"sortComprobantesByDate",
	[td],
	() => {
        let order = true;
        let key = "date";
		let sortedA = td && td.sort((a, b) => {
			let A =(new Date(a[key])).getTime();
			let B = (new Date(b[key])).getTime();
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);




export const calcSubmayor = (td, employee, subEmployee) =>
callbackOnMemory(
	"calcSubmayor",
    [td, employee, subEmployee],() => {
        let order = true;
        let key = "date";
		let sortedA = td.sort((a, b) => {
			let A =(new Date(a[key])).getTime();
			let B = (new Date(b[key])).getTime();
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });

       let prevBal = 0;

        const saldo = (item)=>{
            let itm = {...item};
            if(item?.employee?.type==="credit"){
                itm["saldo"] = prevBal +  (itm["credit"]*1) - (itm["debit"]*1); 
            }else{
                itm["saldo"] = prevBal + (itm["debit"]*1) - (itm["credit"]*1); 
            }
           
            prevBal = itm["saldo"];
            return itm;
        }

		return sortedA.map((d)=>saldo(d));
	}
);





