import React, { useEffect, useState, useRef } from "react";



import {  gen16CodeId, parseDate, updStore, getStore, extractObjFlds, isSuperUser } from "../../../stores/Utils";



import "./styles.css"
import { AccountLabelDetail_Cmpt, SvgIcon_Cmpt } from "../../../components";
import { sortComprobantesByType } from "../services/server_fetching";
import { Comprobante2Pdf } from "../services/export_service";
import { OpenModal } from "../../../components/DialogHRM";
import { UpdComprobante_Cmpt } from "..";
import { getall_employee } from "../../Employees/services/server_fetching";


const AccountLabelDetail = AccountLabelDetail_Cmpt();
const UpdComprobante = UpdComprobante_Cmpt();



const Svg = SvgIcon_Cmpt();



const ViewComprobante  = ({ data, item }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);


	
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);

			if(item?.employeeId){
				if(!getStore("employees_ids")?.[item?.employeeId]){
					getall_employee(updObs);
				}
			}
			set_obs(gen16CodeId())
		}
	});

	
	const send2pdf = () =>{
		Comprobante2Pdf(item)
	}


	const updObs = () =>{
		set_obs(gen16CodeId())
	}

	let employeeId = getStore("employees_ids")?.[item?.employeeId]?.name;

	return (
		<div style={{
			maxHeight: "80vh",
			width: "860px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				
			</div>
			<div className="_dsplFlx ">
				<div className="_dsplFlx" style={{marginTop: 20}} >
					<div className="desc_lbl" >
						{"Comprobante:"} 
					</div>
					<div className="name comprobante_id"  style={item.balance !== 0 ?{color:"var(--hrm_palette-selected-nav-text)"}:{}}>
						{item?. comprobanteId}
					</div>
				</div>
				{item.balance !== 0 ?
				<div className="msg_alert">
					<div className="msg_alert_title">
						El comprobante no esta cuadrado
					</div>
				</div>
				:null}
				<div className="flexSpace"/>
				<div className="icon" onClick={send2pdf}>
					<Svg name={"printer"} color={"#282828"}/>
				</div>
			</div>

			
			<div className="_dsplFlx cmp_dtls">
				
				<div className="flexSpace"/>
				<div className="_dsplFlx"  >
					<div className="desc_lbl" >
						{} 
					</div>
					<div className="name">
						{ parseDate(item?. date)}
					</div>
				</div>
			
				
			</div>




			
			<div className="_dsplFlx hdr_bar purple">
				<div className="tr_bar_15">
					Cuenta
				</div>
				<div className="tr_bar_15">
					Sub Cuenta
				</div>
				<div className="tr_bar_40">
					Documento
				</div>
				<div className="tr_bar_15">
					Debito
				</div>
				<div className="tr_bar_15">
					Credito
				</div>
			</div>

			{sortComprobantesByType(item?.operations)?.map((element, in2D) => 
				<ItemRecord element={element} in2D={in2D} key={in2D}/>
			)}

			<div className="cmp_desc">
				<div className="desc_lbl" >
					{"Detalles:"} 
				</div>
				<div className="description">
					{ item?. description} {employeeId}
				</div>
				
				
				
			</div>
			<div className="_dsplFlx cmp_dtls">
				<div className="flexSpace"/>
				<span className="entryType">
					{item.entryName || "Comprobante Manual"}
				</span>
			</div>
			
		</div>
	);
};

export default ViewComprobante;





const ItemRecord = ({element, in2D }) =>{

	const OpenDeleteRecord =  () => {
		if(isSuperUser()){
			let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
			if(Date.now() > lastTimeCallModal){
				let flds = ["document","amount","type","description", "account", "subAccount", "comprobanteId"]

				let frm2Upd = extractObjFlds({...getStore("comprobante_list")[element.operationId]},flds)
				
				updStore("upd_comprobantebyId", frm2Upd);
				updStore("upd_comprobantebyId_bck", JSON.parse(JSON.stringify(frm2Upd)));
				updStore("lastTimeCallModal", Date.now()+320);
				let modaldata = {};
				modaldata["zIndex"] = 450;
				modaldata["height"] = "40vh";
				// data['observeResize']=true;
				modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
				modaldata["content"] = <UpdComprobante confirm={ ()=>{}} item={element} />
				OpenModal(modaldata);
			}
		}
	}


	return (
			<div className="_dsplFlx hdr_bar" key={in2D}>
				<div className="tr_bar_15">
					<AccountLabelDetail 
						data={element.account}
						labelField={"code"}
						infoField={"name"}
					/>
				</div>
				<div className="tr_bar_15">
					<AccountLabelDetail 
						data={element.sub_account}
						labelField={"subAccount"}
						infoField={"subAccountName"}
					/>
				</div>
				<div className="tr_bar_40" onClick={OpenDeleteRecord}>
					{element.document}
				</div>
				<div className="tr_bar_15">
					{(element.debit*1)?.toFixed(2)}
				</div>
				<div className="tr_bar_15">
					{(element.credit*1)?.toFixed(2)}
				</div>
			</div>
	)
}










